.hero{
    height: 60vw;
    /* background-color: gray; */
    background: linear-gradient(180deg, #a6a3a7, #e1ffea22, 60%);
    display: flex;
}
.hero-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* padding-top: 60px; */
    gap: 20px;
    padding-left: 180px;
    line-height: 1.1;
}
.hero-left h2{
    color: #090909;
    font-size: 20px;
    font-weight: 600;

}

.hero-left p{
    color: #171717;
    font-size: 100px;
    font-weight: 700;

}

.hero-hand-icon{
    display: flex;
    align-items: center;
    gap: 20px;

}

.hero-hand-icon img{
    width: 100px;

}

.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background-color: #ff4141;
    color: white;
    font-size: 22px;
    font-weight: 500 ;
}

.hero-right{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
}

