.newLetter{
    width: 60%;
    height: 40vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 140px;
    background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
    gap: 30px;
}

.newLetter h1{
    color: #454545;
    font-size: 55px;
    font-weight: 600;
}

.newLetter p{
    color: #454545;
    font-size: 20px;

}
.newLetter div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background:white ;
    width: 730px;
    height: 70px;
    border-radius: 1px solid #e3e3e3;

}

.newLetter input{
    width: 500px;
    margin-left: 30px;
    border: none;
    outline: none;
    color: #616161;
    font-family: Poppins;
    font-size: 16px;
}

.newLetter div button{
    width: 210px;
    height: 70px;
    border-radius: 80px;
    background-color: black;
    color: white;
    font-size: 16px;
    cursor: pointer;
}